.about-hero {
  padding: 130px 0 100px 0;
  background-image: url(../../img/bg-about-hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__container {
    display: flex;
    gap: 100px;
    align-items: center;
  }

  & .hero__write {
    margin: 0 auto 0 0;
  }

  & .hero__title {
    text-align: left;
    margin-top: 26px;
  }

  & .hero__descr {
    text-align: left;
    margin-top: 25px;
    margin-bottom: 45px;
  }

  & .hero__get {
    margin: 25px auto 0 0;
  }

  &__right {

    & img {
      min-width: 550px;
    }
  }
}
