.about-we {

  padding: var(--offset-padding) 0;

  &__list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 45px;
    margin-top: 40px;
  }

  &__item {
    background-image: url(../../img/bg-about-we.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__text {
    color: #fff;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
  }

  &__img {
    width: 190px;
  }
}

.about-main {
  background-image: url(../../img/bg-about-main.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
