.about-give {
  padding: var(--offset-padding) 0;

  &__wrap {
    display: flex;
    gap: 70px;
    align-items: center;
  }

  &__left {
    width: 50%;
  }

  &__company {
    display: flex;
    gap: 45px;
    margin: 35px 0;
  }

  &__company-item {
    background-color: rgba(25, 98, 100, 0.4);
    border-radius: 20px;
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: #fff;
  }

  &__company-left &__company-top {
    font-size: 18px;
  }

  &__company-left &__company-bottom {
    font-size: 34px;
    font-weight: 600;
  }

  &__company-right &__company-top {
    font-size: 18px;
  }

  &__company-right &__company-bottom {
    font-size: 20px;
    font-weight: 500;
  }

  &__right {
    position: relative;
    width: 50%;
  }

  &__text {
    color: #fff;
    font-size: 18px;
  }

  &__item {
    color: #fff;
    background-color: #181D29;
    border-radius: 20px;
    padding: 15px 20px;
    position: absolute;
    z-index: 99;

    &:nth-child(1) {
      width: 337px;
      top: 2%;
      left: -1%;
    }

    &:nth-child(2) {
      width: 251px;
      top: -17%;
      right: 10%;
    }

    &:nth-child(3) {
      width: 251px;
      top: 55%;
      left: 3%;
    }

    &:nth-child(4) {
      width: 278px;
      top: 48%;
      right: 12%;
    }
  }

  &__bg {
    position: relative;
    top: 10%;
    left: 25%;
    z-index: 10;
  }

  &__subhead {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
    text-align: center;
    display: block;
  }

  &__subdescr {
    font-weight: 300;
    font-size: 16px;
    text-align: center;
    display: block;
  }

  &__btn {
    margin: 0 auto;
    margin-top: 90px;
  }
}
