.geography {

  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-geography.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @media (max-width:650px) {
    & {
      padding: var(--offset-padding-m) 0;
    }
  }

  &__subtitle {
    text-align: center;
  }

  &__title {
    text-align: center;
  }

  &__descr {
    text-align: center;
    margin-top: 15px;

    @media (max-width:800px) {
      & {
        font-size: 16px;
      }
    }
  }
}

.map {
  margin-top: 20px;
  position: relative;
}

.point {
  width: 25px;
  height: 25px;
  cursor: pointer;
  transition: transform 0.3s ease;
  position: absolute;

  @media (max-width:1200px) {
    & {
      width: 20px;
      height: 20px;
    }
  }

  @media (max-width:950px) {
    & {
      width: 15px;
      height: 15px;
    }
  }

  @media (max-width: 500px) {
    & {
      width: 12px;
      height: 12px;
    }
  }

  &__br {
    top: 65.5%;
    left: 33.7%;
  }

  &__ru {
    top: 22.8%;
    left: 73.5%;
  }

  &__ca {
    top: 9.9%;
    left: 21.5%;
  }

  &__in {
    top: 54.2%;
    left: 68.6%;
  }

  &__uz {
    top: 36.9%;
    left: 64.3%;
  }

  &__bn {
    top: 48.8%;
    left: 72.3%;
  }

  &__pl {
    top: 32.2%;
    left: 54.3%;
  }

  &__tr {
    top: 40.2%;
    left: 56.9%;
  }

  &__kz {
    top: 31%;
    left: 63%;
  }

  &__az {
    top: 40%;
    left: 60.4%;
  }
}

.tooltip {
  position: absolute;
  bottom: 33px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9;
  background-color: #fff;
  border-radius: 10px;
  padding: 10px 20px;
  color: #000;
  font-size: 13px;
  width: max-content;
  opacity: 0;
  visibility: hidden;

  @media (max-width:950px) {
    & {
      font-size: 11px;
      padding: 7px 14px;
    }
  }

  @media (max-width: 500px) {
    & {
      display: none;
    }
  }

  &:after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -6px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 8px 0 8px;
    border-color: #fff transparent transparent transparent;

  }

  &__country {
    font-weight: 600;
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 3px;
    margin-top: 7px;
  }

  &__number {
    font-weight: 600;
    display: block;
  }

  &__descr {
    display: block;
    color: #667085;
    font-weight: 400;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 3px;
  }

  &__top {
    text-align: center;

    & img {
      width: 20px;
      height: 20px;
      margin: 0 auto 3px auto !important;

      @media (max-width:950px) {
        & {
          width: 15px;
          height: 15px;
        }
      }
    }
  }
}

.point:hover .point__flag {
  animation: pulse 0.5s ease-in-out forwards;
}

.point:hover .tooltip {
  opacity: 1;
  visibility: visible;

  @media (max-width:700px) {
    & {
      opacity: 0;
      visibility: hidden;
    }
  }
}

@keyframes pulse {

  0%,
  100% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.2);
  }
}
