.services-content {
  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-services-content.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__list {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 120px;
    row-gap: 70px;
  }

  &__wrap {
    position: relative;
    background: linear-gradient(to bottom, #202C35 60%, transparent 40%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    border-radius: 40px;
    transition: 0.1s ease-in all;

    &:hover {
      background: linear-gradient(to bottom, #0BCACA 60%, transparent 40%);
      transition: 0.1s ease-in all;

      & .services-content__arrow {
        background-color: #196264;
        transition: 0.1s ease-in all;
      }
    }
  }

  &__last {
    position: relative;
    background: none;
    padding: 20px;
    color: #fff;
    display: flex;
    flex-direction: column;
    gap: 25px;
    justify-content: center;

    &:hover {
      background-image: none;
    }
  }

  &__arrow-long {
    position: absolute;
    height: 130px;
    top: 50%;
    transform: translateY(-50%);
    left: -20px;
  }

  &__border {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    border-radius: 40px;
    border-top: 1px solid #8F959A;
    border-left: 1px solid #8F959A;
    border-right: 1px solid #8F959A;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    padding: 25px 20px 20px 20px;
  }

  &__img {
    position: relative;
    z-index: 10;
    width: 100%;
    padding-top: 25px;
    border-top: 1px solid #8F959A;
  }

  &__head {
    font-size: 26px;
    color: #fff;
    font-weight: 500;
  }

  &__new {
    background-color: #196264;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 9px;
    padding: 5px 10px;
    display: block;
    text-transform: uppercase;
    font-weight: 300;
  }

  &__arrow {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: #1D2939;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 35px;
    border-radius: 100%;
    transition: 0.1s ease-in all;

    & img {
      width: 30px;
      height: 30px;
    }
  }

  &__btn {
    margin: 0 auto;
  }
}

.services-result {

  max-width: 1400px;
  width: 100%;
  background-image: url(../../img/bg-services-result.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50px;
  padding: 60px 200px;
  margin: 0 auto;
  margin-top: 150px;


  &__title {
    text-align: center;
  }

  &__descr {
    text-align: center;
    margin: 25px 0;
  }

  &__btn {
    margin: 0 auto;
  }
}
