.vacancies-content {

  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-services-content.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__list {
    display: flex;
    flex-direction: column;
    gap: 40px;
    margin-top: 60px;
  }

  &__item {
    position: relative;
    background-color: #1E232D;
    padding: 30px 40px;
    border-radius: 20px;
    color: #fff;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__head {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 5px;
  }

  &__depiction {
    color: #BDBDBD;
    font-size: 16px;
    font-weight: 400;
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__price {
    display: block;
  }

  &__descr {
    font-size: 18px;
    color: #fff;
    font-weight: 400;
    max-width: 1200px;
    width: 100%;
    margin: 25px auto 25px 0;
  }

  &__sublist {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__subitem {
    display: flex;
    align-items: center;
    gap: 7px;
    font-size: 18px;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    gap: 50px;
  }

  &__btn {
    height: max-content;
    margin-top: auto;
  }

  &__border-left{
    border-left: 2px solid #fff;
    border-bottom: 2px solid #fff;
    border-bottom-left-radius: 20px;
    width: 55px;
    height: 55px;
    position: absolute;
    left: 0;
    bottom: 0;
  }

    &__border-right{
    border-right: 2px solid #fff;
    border-top: 2px solid #fff;
    border-top-right-radius: 20px;
    width: 55px;
    height: 55px;
    position: absolute;
    right: 0;
    top: 0;
  }
}
