.about-advantages {
  padding: var(--offset-padding) 0;

  &__subtitle {
    text-align: center;
  }

  &__title {
    text-align: center;
    margin-bottom: 10px;
  }

  &__descr {
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 35px;
    margin-top: 50px;
  }

  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    background-color: #0D1D29;
    border-radius: 20px;
    padding: 15px;
  }

  &__head {
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    padding-bottom: 10px;
    position: relative;

    &::after {
      content: "";
      height: 2px;
      width: 94px;
      background-color: #75F7E4;
      border-radius: 10px;
      position: absolute;
      left: 50%;
      bottom: 0px;
      transform: translateX(-50%);
    }
  }

  &__subdescr {
    color: #A8A8A8;
    font-size: 14px;
    text-align: center;
  }
}

.about-result {

  max-width: 1400px;
  width: 100%;
  background-image: url(../../img/bg-about-result.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50px;
  padding: 60px 200px;
  margin: 0 auto;
  margin-top: 100px;


  &__title {
    text-align: center;
  }

  &__descr {
    text-align: center;
    margin: 25px 0;
  }

  &__btn {
    margin: 0 auto;
  }
}
